.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  background-color: $orange;
  color: #fff;
  border-radius: 8px;
  height: 46px;
  text-align: center;
  padding: 13px 24px;
  cursor: pointer;
  transition: background-color .3s;
  @include hover-effect;

  &--border {
    border: 1px solid #C56940;
    background-color: transparent;
    color: #C56940;
  }

  &--blue {
    border: 1px solid #274D74;
    background-color: #274D74;
    color:#fff;
    &.btn--border{
      background-color:#fff;
      color:#274D74;
    }
  }


}

.nav-next,
.nav-prev {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: inline-block;
  box-shadow: 0px 4px 10px -2px #00000040;
  cursor: pointer;

  &.swiper-button-lock{
    display: none;
  }

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M17.4177 5.41772L16.3487 6.48681L21.1059 11.244H0V12.756H21.1059L16.3487 17.5132L17.4177 18.5822L24 12L17.4177 5.41772Z' fill='black'/%3e%3c/svg%3e ");
    width: 24px;
    height: 24px;
    display: inline-block;
  }

  &.swiper-button-disabled {
    opacity: .5;
    cursor: auto;
  }
}

.nav-prev {
  &::before {
    transform: translate(-50%, -50%) rotate(180deg);
  }

  & + .nav-next {
    margin-left: 30px;
  }
}