@import "fonts";
@import "null";
@import "mixins";
@import "./vendors/swiper";


$blue: #274D74;
$orange: #C56940;
$black: #011222;
$lGreen: #708990;
$sand: #D5D0CA;
$lGrey: #F2F3F7;

$laptop: 1240px;
$tablet: 991.92px;
$mobile: 767.92px;
$mobileMedium: 575.98px;
@import "forms";
@import "typegr";
@import "btns";
@import "modal";
@import "./layout/header";
@import "./layout/footer";


body.blocked {
  overflow: hidden;
}

body.overlayed {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: .2;
    z-index: 100;
    cursor: pointer;
  }
}

body.lock {
  overflow: hidden;
}

.container {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  max-width: 1296px;
  @media (max-width: 1376px) {
    max-width: 100%;
    padding: 0 40px;
  }
  @media (max-width: 768px) {
    max-width: 688px;
    padding: 0;
  }
  @media (max-width: 718.92px) {
    max-width: 100%;
    padding: 0 15px;
  }
}

body {
  font-family: "Inter", sans-serif;
  color: $black;
  font-size: 14px;
  line-height: 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-weight: 400;
  letter-spacing: 0.02em;
}

main {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}

section {
  margin: 100px 0;
  @media (max-width: $tablet) {
    margin: 80px 0;
  }
  @media (max-width: $mobile) {
    margin: 60px 0;
  }
}

.welcome-banner-section {
  margin-top: 0;
}

.welcome-banner {
  position: relative;
  background-color: $blue;
  border-radius: 10px;
  overflow: hidden;
  min-height: 440px;
  @media (max-width: $tablet) {
    min-height: 380px;
  }
  @media (max-width: $mobileMedium) {
    min-height: auto;
    height: unset;
    max-height: unset;
  }

  &__info {
    position: relative;
    z-index: 1;
    color: #fff;
    max-width: 540px;
    padding: 64px 0 100px 64px;
    @media (max-width: $tablet) {
      padding: 40px;
    }
    @media (max-width: $mobile) {
      padding: 45px 20px;
    }
    @media (max-width: $mobileMedium) {
      padding-bottom: 120px;
    }
  }

  &__picture {
    position: absolute;
    right: 0;
    bottom: 0;
    top: 24%;
    @media (max-width: $tablet) {
      max-width: 532px;
    }
    @media (max-width: $mobileMedium) {
      max-width: unset;
      top: auto;
      left: 0;
      height: 41%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    margin-bottom: 8px;
  }

  &__subtitle {
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 17px;
    @media (max-width: $tablet) {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__labels {
    display: flex;
    flex-wrap: wrap;
    margin: -9px;
    margin-bottom: 40px;
    @media (max-width: $mobile) {
      margin: -4px;
      margin-bottom: 16px;
    }

    span {
      display: inline-block;
      background-color: #E3E1EB;
      padding: 4px 8px;
      margin: 9px;
      border-radius: 5px;
      color: $blue;
      font-weight: 500;
      @media (max-width: $tablet) {
        font-size: 12px;
        font-weight: 500;
        line-height: 14.52px;
      }
      @media (max-width: $mobile) {
        margin: 4px;
      }
    }
  }
}

.important-webinars {
  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__banner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    border: 1px solid #274D74;
    padding: 25px;
    border-radius: 10px;
    text-align: center;
    @media (max-width: $tablet) {
      padding: 32px;
    }
    @media (max-width: $mobile) {
      padding: 24px;
    }

    p, h2, h3, h4, h5, h6 a {
      margin-bottom: 1em;
    }
  }

  &__subtitle {
    margin-bottom: 16px;
    @media (max-width: $mobile) {
      margin-bottom: 8px;
    }
  }

  &__text {
    margin-bottom: 32px;
  }

  &__content {
    display: flex;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
  }

  &__info {
    width: 414px;
    flex-shrink: 0;
    margin-right: 34px;
    @media (max-width: $tablet) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 32px;
    }

  }

  &__items {
    width: calc(100% - 448px);
    margin-right: -30px;
    @media (max-width: 1336px) {
      width: calc(100% - 408px);
      margin-right: -40px;
    }
    @media (max-width: $tablet) {
      width: 100vw;
      margin-left: calc(50% - 50vw);
    }


    .swiper-slide {
      width: 400px;
      height: auto;
      @media (max-width: $tablet) {
        width: calc(50vw - 57px);
        min-width: 265px;
      }
      @media (max-width: $mobile) {
        width: 319px;
      }
      @media (max-width: 718.92px) {
        width: calc(50% - 31px);
      }

      .webinar-card {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.webinar-card {
  width: 400px;
  background-color: #F5F4FA;
  border-radius: 10px;
  padding: 20px 20px 48px;
  @media (max-width: $tablet) {
    width: calc(50vw - 57px);
    min-width: 265px;
  }
  @media (max-width: $mobile) {
    width: 319px;
  }
  @media (max-width: 718.92px) {
    width: calc(50% - 31px);
  }

  &__picture {
    height: 252px;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      height: 27vw;
      border-radius: 6px;
      overflow: hidden;
      min-height: 150px;
    }


    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    margin-bottom: 32px;
  }

  &__price {
    margin-top: 16px;
  }

  &__new-price {
    font-size: 18px;
    font-weight: 800;
    line-height: 24px;
    white-space: nowrap;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__old-price {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    color: #C2C2C2;
    text-decoration: line-through;
    margin-left: 19px;
    white-space: nowrap;
    @media (max-width: $mobile) {
      font-size: 12px;
      line-height: 14px;
      margin-left: 10px;
    }
  }

  &__labels {
    display: flex;
    justify-content: space-between;
    color: #685F78;
    padding-bottom: 18px;
    border-bottom: 1px solid #C8CDD3;
    @media (max-width: $tablet) {
      font-size: 12px;
      line-height: 14.52px;
    }
  }

  &__btn {
    margin-top: 32px;
  }

  &__contains {
    position: relative;
    display: flex;
    align-items: center;

