.features {
  &__content {
    display: flex;
    margin: -24px;
    @media (max-width: $laptop) {
      margin: -12px;
    }
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: -15px;
    }
    @media (max-width: $mobileMedium) {
      margin: 0 0 -12px;
    }

    .feature-item {
      width: calc(25% - (48px));
      margin: 24px;
      flex-grow: 1;
      @media (max-width: $laptop) {
        width: calc(25% - (24px));
        margin: 12px;
      }
      @media (max-width: $tablet) {
        width: calc(50% - 30px);
        margin: 15px;
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
        margin: 0 0 12px;
      }
    }
  }

  &__title {
    margin-bottom: 32px;
    @media (max-width: $mobileMedium) {
      margin-bottom: 24px;
    }
  }
}

.feature-item {
  padding: 30px;
  background: #F2F3F7;
  border-radius: 20px;
  @media (max-width: $mobileMedium) {
    padding: 24px 24px 60px;
  }

  &__icon {
    height: 48px;
    margin-bottom: 32px;

    svg {
      max-height: 100%
    }
  }

  &__title {
    margin-bottom: 12px;
  }
}

.cards-slider {
  &__title-block {
    display: flex;
    align-items: center;
    margin-bottom: 32px;
    @media (max-width: $mobileMedium) {
      margin-bottom: 24px;
    }
  }

  &__navigation {
    display: flex;
    @media (max-width: $mobileMedium) {
      display: none;
    }

  }

  &__title {
    margin-right: auto;
  }

  &__btn {
    margin-right: 64px;
    @media (max-width: $mobile) {
      display: none;
    }
  }

  &__content {
    margin: 0 calc(50% - 50vw);

    .swiper-slide {
      width: 400px;
      height: auto;
      @media (max-width: $tablet) {
        width: calc(50% - 57px);
        min-width: 265px;
      }
      @media (max-width: $mobile) {
        width: 319px;
      }
      @media (max-width: 718.92px) {
        width: calc(50% - 31px);
      }

      .webinar-card {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.partners {
  &__title-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 32px;
    @media (max-width: $mobileMedium) {
      margin-bottom: 24px;
    }
  }

  &__navigation {
    display: flex;
    @media (max-width: $mobileMedium) {
      display: none;
    }
  }

  &__content {
    margin: 0 calc(50% - 50vw);

    .swiper-slide {
      width: 288px;
      height: auto;

      .partner-item {
        height: 100%;
      }
    }
  }
}

.partner-item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 45px;

  &__wrapper {
    padding: 87px 8px 28px;
    background-color: #E3E1EB;
    border-radius: 10px;
    flex-grow: 1;
    text-align: center;
  }


  &__picture {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    display: flex;
    width: 100px;
    height: 100px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__title {
    margin-bottom: 8px;
  }

  &__label {
    font-size: 12px;
    font-weight: 400;
    line-height: 14.52px;
    letter-spacing: 0.02em;
    color: $orange;
    margin-bottom: 12px;
  }
}

.reviews {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
  }

  &__slider-wrapper {
    display: flex;
    flex-direction: column;
    @media (max-width: $tablet) {
      flex-direction: column-reverse;
    }
  }

  &__slider-wrapper,
  &__title-box {
    flex-shrink: 0;
    width: calc(50% - 24px);
    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 12px;
    }
  }

  &__navigation {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      justify-content: center;
      margin-bottom: 0;
      margin-top: 35px;
    }
    @media (max-width: $mobile) {
      margin-top: 24px;
    }

    .nav-next {
      @media (max-width: $mobile) {
        margin-left: 24px;
      }
    }
  }

  &__swiper {
    width: 100%;
    flex-grow: 1;
  }

  &__title-box {
    position: relative;
    display: flex;
    background-color: #F2F3F7;
    padding: 48px;
    border-radius: 20px;
    margin-top: 82px;
    @media (max-width: $tablet) {
      margin-top: 0;
      margin-bottom: 30px;
