.categories {
  display: flex;
  margin-left: -23px;
  margin-right: -18px;
  overflow-x: auto;
  max-width: 100%;
  padding-bottom: 10px;
  @media (max-width: $mobile) {
    margin-left: 0;
    margin-right: 0;
  }

  &__item {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.02em;
    margin: 0 18px;
    white-space: nowrap;
    @media (max-width: $mobile) {
      margin: 0 14px;
      font-size: 14px;
      line-height: 20px;
    }

    label {
      position: relative;
      cursor: pointer;
      padding: 5px;
      @include hover-effect;
    }

    input {
      display: none;

      &:checked + label {
        &:hover {
          opacity: 1;
        }

        &::before {
          content: '';
          position: absolute;
          height: 3px;
          background-color: $orange;
          bottom: 0;
          left: 4px;
          right: 4px;
          width: calc(100% - 8px);
        }
      }
    }
  }
}

.catalog-sort {
  position: relative;
  display: inline-block;

  &__val {
    margin-right: 6px;
  }

  &__body {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    border: 1px solid #D8DAE5;
    background-color: #fff;
    border-top: none;
    border-radius: 0 0 4px 4px;
    padding: 6px 8px;


    &[data-active] {
      opacity: 1;
      z-index: 1;
      visibility: visible;
    }

    &[data-active] {
      & + .catalog-sort__current {
        border-bottom-color: transparent;
        border-radius: 4px 4px 0 0;
      }
    }
  }

  &__current {
    position: relative;
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.02em;
    padding: 6px 8px;
    cursor: pointer;
    border: 1px solid #D8DAE5;
    border-radius: 4px;
    width: 100%;
    white-space: nowrap;

    &::after {
      content: '';
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-left: 8px;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M13.5 6L8 11L2.5 6' stroke='%23051B44' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
    }
  }

  &__item-to-choose {
    padding: 4px 0;
    cursor: pointer;
  }
}

.pagination {
  display: flex;
  justify-content: center;

  a {
    @include hover-effect;
  }

  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__prev {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;

    &::before {
      content: '';
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-right: 8px;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M15.5 8.5H0.5' stroke='%23051B44' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M5.5 3.5L0.5 8.5L5.5 13.5' stroke='%23051B44' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
    }
  }

  &__pages {
    margin: 4px;
  }

  &__page {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    margin: 4px;
    border-radius: 4px;

    &--active {
      background-color: $blue;
      color: #fff;
    }
  }

  &__next {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;

    &::after {
      content: '';
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-left: 8px;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='17' height='16' viewBox='0 0 17 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip0_622_1841)'%3e%3cpath d='M1 8.5H16' stroke='%23051B44' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M11 3.5L16 8.5L11 13.5' stroke='%23051B44' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/g%3e%3cdefs%3e%3cclipPath id='clip0_622_1841'%3e%3crect width='16' height='16' fill='white' transform='translate(0.5)'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e ");
    }
  }
}

