    @media (max-width: $mobile) {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 20.78px;
    }
  }

  &__input {
    display: block;

    & + .filter-block__input {
      margin-top: 8px;
    }

    input {
      display: none;

      &:checked + label {
        &::after {
          content: '';
          width: 11px;
          height: 9px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 3px;
          display: inline-block;
