        }
      }
    }

    label {
      position: relative;
      display: flex;
      align-items: center;
      padding: 4px 0;
      padding-left: 20px;
      cursor: pointer;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 16px;
        height: 16px;
        display: inline-block;
        border: 1px solid #708990;
        border-radius: 4px;
        margin-right: 8px;
      }
    }
  }
}

.module-welcome {
  &__wrapper {
    display: flex;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
  }

  &__title {
    margin-bottom: 16px;
    @media (max-width: $tablet) {
      margin-bottom: 12px;
    }
  }

  &__btn {
    margin-top: 40px;
    @media (max-width: $tablet) {
      margin-top: 30px;
    }
    @media (max-width: $mobile) {
      margin-top: 20px;
    }
  }

  &__picture {
    width: 53%;
    flex-shrink: 0;
    margin-right: 50px;
    @media (max-width: $tablet) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
    }

    img {
      width: 100%;
      height: auto;
      border-radius: 10px;
      overflow: hidden;
    }
  }
}

.plan {
  counter-reset: count-item;

  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }
}

.plan-item {

  &__title {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #708990;
    padding: 12px 25px;
    border-radius: 10px;
    color: #fff;

    &::after {
      counter-increment: count-item; /* Инкрементирует счётчик*/
      content: counter(count-item);
      font-size: 40px;
      font-weight: 900;
      line-height: 48.41px;
      margin-left: 20px;
    }
  }

  &__body {
    background: #FAFAFA;
    border-radius: 10px;
    font-size: 14px;
    line-height: 20px;
    padding: 24px;
    margin-top: 16px;

    p + p,
    span + p,
    div + p {
      margin-top: 1em;
    }
  }

  & + .plan-item {
    margin-top: 32px;
  }
}

.pic-and-text {
  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__media {
    width: 45%;
    flex-shrink: 0;
    margin-right: 48px;
    @media (max-width: $tablet) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
    }

    img, video {
      width: 100%;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
    }
  }

  &__subtitle {
    margin-bottom: 16px;
  }

  &__btn {
    margin-top: 40px;
  }

  &__content {
    display: flex;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
  }

  &__text {
    p + p,
    span + p,
    div + p {
      margin-top: 1em;
    }
  }

  &--sertificat {
    .pic-and-text {
      &__content {
        @media (max-width: $tablet) {
          flex-direction: column-reverse;
        }
      }

      &__media {
        @media (max-width: $tablet) {
          margin-bottom: 0;
        }
      }

      &__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        border: 1px solid #85A3B1;
        padding: 46px;
        border-radius: 10px;
        @media (max-width: $tablet) {
          padding: 24px;
          margin-bottom: 24px;
        }
      }
    }
  }
}

.telegram-news {
  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__slider {
    @media (max-width: 1141px) {
      margin: 0 calc(50% - 50vw);
    }

    .swiper-slide {
      @media (max-width: 1141px) {
        width: 328px;
      }
    }
  }
}


.telegram-news-item {
  padding: 24px 24px 32px;
  border: 1px solid #85A3B1;
  border-radius: 10px;
  overflow: hidden;

  &__top {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
  }

  &__link {
    color: $orange;
    font-size: 12px;
    font-weight: 400;
    line-height: 14.52px;
  }

  &__ava {
    display: inline-block;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 40px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__text {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    p + p {
      margin-top: 1em;
    }
  }

  &__more {
    margin-top: 32px;
  }
}

.materials {
  &__title-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__link {
    @media (max-width: $mobile) {
      display: none;
    }
  }
}

.materials-item {
  display: flex;
  align-items: center;

  & + .materials-item {
    margin-top: 24px;
    @media (max-width: $tablet) {
      margin-top: 16px;
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: $mobileMedium) {
      margin-bottom: 12px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px 24px;
    background: #FAFAFA;
    @include hover-effect;
    @media (max-width: $mobileMedium) {
      flex-direction: column;
      padding: 20px 16px;
      text-align: center;
    }
  }

  &__title {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
    margin-left: 16px;
    @media (max-width: $mobileMedium) {
      margin-left: 0;
    }
  }

  &__label {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: $orange;
  }

  &__label {
    margin-left: auto;
    @media (max-width: $mobileMedium) {
      margin: 24px auto 0;
    }
  }
}

.articles-prev {
  &__bottom {
    display: none;
    @media (max-width: $mobile) {
      display: block;
      margin-top: 40px;
    }
  }

  &__title-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: 50% calc(50% - 48px);
    grid-column-gap: 48px;
    grid-row-gap: 30px;
    @media (max-width: $laptop) {
      grid-template-columns: 40% calc(60% - 48px);
    }
    @media (max-width: $tablet) {
      grid-row-gap: 24px;
    }

    .article-prev {
      grid-column: 2;
      @media (max-width: $tablet) {
        grid-column: 1/3;
      }
    }

    .article-prev:nth-child(1) {
      grid-column: 1;
      grid-row: 1/3;
      @media (max-width: $tablet) {
        grid-column: 1/3;
      }

      .article-prev {
        &__wrapper {
          flex-direction: column;
        }

        &__photo {
          margin-right: 0;
          margin-bottom: 24px;
          width: 100%;
        }
      }
    }
  }

  &__more-link {
    @media (max-width: $mobile) {
      display: none;
    }
  }
}

.article-prev {
  &__wrapper {
    display: flex;
    height: 100%;
    @media (max-width: $mobile) {
      flex-direction: column;
    }
  }

  a {
    @include hover-effect;
  }


  &__photo {
    border-radius: 10px;
    overflow: hidden;
    margin-right: 48px;
    width: 50%;
    flex-shrink: 0;
    @media (max-width: $tablet) {
      margin-right: 30px;
    }
    @media (max-width: $mobile) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    display: inline-block;
    margin-bottom: 8px;
  }

  &__text {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }

  &__date {
    position: relative;
    margin-top: 24px;
    font-size: 14px;
    line-height: 16.94px;
    color: #685F78;

    &::before {
      content: '';
      position: relative;
      top: 2px;
