.materials-page {
  &__title {
    font-size: 24px;
    font-weight: 600;
    line-height: 29.05px;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 19.36px;
      margin-bottom: 24px;
    }
  }

  &__categories {
    margin-bottom: 22px;
  }

  &__items {
    padding-top: 48px;
    @media (max-width: $tablet) {
      padding-top: 32px;
    }
  }

  &__pagination {
    margin-top: 32px;
  }
}

.articles-page {
  &__pagination {
    margin-top: 32px;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -24px -48px;
    @media (max-width: $laptop) {
      margin: 0 -12px -24px;
    }
    @media (max-width: $tablet) {
      margin: 0 -12px -24px;
    }
    @media (max-width: $mobile) {
      margin: 0 0 -24px;
    }

    .resource-item {
      width: calc(25% - 48px);
      margin: 0 24px 48px;
      @media (max-width: $laptop) {
        width: calc(33% - 24px);
        margin: 0 12px 24px;
      }
      @media (max-width: $tablet) {
        width: calc(50% - 24px);
      }
      @media (max-width: $mobile) {
        width: calc(100%);
        margin: 0 0 24px;
      }
    }
  }
}