h1, .h1 {
  font-family: 'Rubik';
  font-size: 48px;
  font-weight: 700;
  line-height: 56.88px;
  letter-spacing: 1px;
  @media (max-width: $tablet) {
    font-size: 32px;
    font-weight: 700;
    line-height: 37.92px;
  }
  @media (max-width: $mobile) {
    font-size: 32px;
    font-weight: 700;
    line-height: 37.92px;
  }
}

h2, .h2 {
  font-family: 'Rubik';
  font-size: 36px;
  font-weight: 700;
  line-height: 42.66px;
  letter-spacing: 1px;
  @media (max-width: $tablet) {
    font-size: 24px;
    font-weight: 700;
    line-height: 28.44px;
    letter-spacing: 0.8999999761581421px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    font-weight: 700;
    line-height: 28.44px;
    letter-spacing: 0.8999999761581421px;
  }
}

h3,.h3{
  font-size: 18px;
  font-weight: 600;
  line-height: 21.78px;
  @media (max-width: $tablet){
    font-size: 16px;
    font-weight: 600;
    line-height: 19.36px;
  }
}