.footer {
  background-color: $blue;
  color: #fff;
  letter-spacing: 0em;

  &__content {
    display: flex;
    padding: 68px 0 84px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
    @media (max-width: $mobile) {
      padding: 48px 0;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    padding: 16px 0;
    color: #D5D0CA;
    font-size: 10px;
    font-weight: 300;
    line-height: 12.1px;
    margin: 0 -6px;

    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: 0 0 0;
      padding-bottom: 0;
    }

    & > a, & > span {
      margin: 0 6px;
      @media (max-width: $tablet) {
        margin: 0 0 16px;
      }
    }
  }

  &__logo-block {
    margin-right: auto;
    @media (max-width: $tablet) {
      margin-right: 11%;
    }
    @media (max-width: $mobile) {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      margin-right: 0;
      margin-bottom: 48px;
    }
  }

  &__contacts {

  }

  &__logo {
    display: flex;
    margin-bottom: 24px;
    @media (max-width: $mobile) {
      margin-right: 10%;
    }
  }


  &__subscription-block {
    width: 400px;
    flex-shrink: 0;
    margin-left: auto;
    @media (max-width: $tablet) {
      width: 100%;
      margin-top: 64px;
    }
  }

  &__nav-block {
    margin-right: 10px;
    @media (max-width: $tablet) {
      max-width: 30%;
    }
    @media (max-width: $mobile) {
      max-width: unset;
      width: calc(50% - 30px);
    }

    & + .footer__nav-block {
      margin-left: auto;
      @media (max-width: $tablet) {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-grow: 1;
      }
    }
  }

  &__subtitle {
    margin-bottom: 24px;
  }

  &__nav {
    li + li {
      margin-top: 2px;
    }

    a {
      display: inline-block;
      padding: 5px;
      margin-left: -5px;
      @include hover-effect;
    }
  }

  .subscribe-box {
    margin-top: 16px;
  }
}

.footer-link {
  display: flex;
  align-items: center;

  &__icon {
    display: inline-flex;
    margin-right: 8px;
  }

  & + .footer-link {
    margin-top: 12px;
  }
}

.subscribe-box {
  &__form {
    position: relative;
  }

  .form-input__error-text {
    position: relative;
  }

  &__submit {
    position: absolute;
    right: 5px;
    top: 5px;
    bottom: 5px;
    height: 40px;
  }

  .form-input {
    input {
      color: #685F78;
      padding-right: 130px;

      &::placeholder {
        color: #685F78;
      }
    }
  }

  &__agree {
    font-size: 12px;
    font-weight: 300;
    line-height: 14.52px;
    margin-top: 9px;
  }
}