.header {
  z-index: 99;
  @media (max-width: $tablet) {
    height: 128px;
  }

  &__content {
    display: flex;
    align-items: center;
    padding: 24px 0;
  }

  &__logo {
    display: flex;
    @media (max-width: $tablet){
      svg{
        max-width: 120px;
      }
    }
  }

  &__btns {
    @media (max-width: $tablet) {
      margin-left: auto;
    }
    .btn{
      &:nth-child(1){
        @media (max-width: $mobileMedium){
          display: none;
        }
      }
    }

    .btn + .btn {
      margin-left: 24px;
      @media (max-width: $mobileMedium){
        margin-left: 0;
      }
    }
  }

  &__nav {
    margin: 0 auto;
    @media (max-width: $tablet) {
      display: none;
    }

    ul {
      display: flex;
      align-items: center;
      @media (max-width: $tablet) {
        flex-wrap: wrap;
        padding: 0 40px;
        margin: 40px auto;
        justify-content: space-between;
      }
      @media (max-width: 768px) {
        max-width: 688px;
        padding: 0;
      }
      @media (max-width: 718.92px) {
        max-width: 100%;
        padding: 0 15px;
      }
    }

    li {
      @media (max-width: $tablet) {
        width: calc(50% - 30px);
        text-align: center;
        margin-bottom: 24px;
      }
      @media (max-width: $mobileMedium){
        width: 100%;
      }
    }


    li + li {
      margin-left: 22px;
      @media (max-width: $tablet) {
        margin-left: 0;
      }
    }

    a {
      position: relative;
      display: inline-block;
      white-space: nowrap;
      padding: 5px;
      @media (max-width: $mobileMedium){
        white-space: unset;
      }

      span {
        position: relative;
        display: inline-block;

        &::after {
          content: '';
          position: absolute;
          width: 0;
          background-color: $orange;
          bottom: 0;
          left: 0;
          top: calc(100% + 3px);
          height: 2px;
          border-radius: 10px;
          transition: width .3s;
          pointer-events: none;
        }
      }


      &:hover {
        span::after {
          width: 100%;
          right: auto;
          @media (max-width: $tablet) {
            content: none;
          }
        }
      }

      &[data-current] {
        span::after {
          width: 100%;
          right: auto;
          @media (max-width: $tablet) {
            content: '';
          }
        }
      }
    }
  }

  .header-burger-icon {
    display: none;
    @media (max-width: $tablet) {
      display: inline-flex;
      flex-direction: column;
      margin-left: 20px;
      cursor: pointer;
      padding: 5px;
      span {
        display: inline-block;
        background: #D5D0CA;
        width: 36.67px;
        height: 5.5px;
        border-radius: 10px;
        transition: all .3s;

        & + span {
          margin-top: 5.5px;
        }
      }
    }
  }


  &__wrapper {
    background: #fff;
    position: relative;
  }

  &[data-open] {
    @media (max-width: $tablet) {

      &::before{
        content:'';
        z-index: 0;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #8A8A8AB2;
      }
      .header {
        &__nav {
          display: block;
          position: fixed;
          top:0;
          left: 0;
          right: 0;
          bottom: 0;
          padding-top: 128px;

          a {
            background: #fff;
            width: 100%;
            font-size: 16px;
            font-weight: 600;
            line-height: 19.36px;
            padding: 30px 20px;
            border-radius: 10px;
            box-shadow: 0px 5px 13px 0px #C4D0D933;
          }

          ul {
            flex-wrap: wrap;
          }
        }
      }
      .header-burger-icon {
        span {
          &:nth-child(2) {
            opacity: 0;
            position: absolute;
          }

          &:nth-child(1) {
            position: relative;
            top: 5px;
            background:#000;
            transform: rotate(45deg);
          }

          &:nth-child(3) {
            position: relative;
            bottom: 6px;
            background:#000;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
  &--scrolled{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    animation: slide-down .6s;
    & + main {
      margin-top: var(--header-height);
    }
    .header__wrapper{
      border-radius: 0 0 10px 10px;
      box-shadow: 0px 6px 12px 0px #9DA8B266;
    }
  }
}

.header-top {
  position: relative;
  background-color: #F7F6F4;

  &__wrapper {
    display: flex;
    justify-content: flex-end;
    padding: 8px 0;
    @media (max-width: $tablet) {
      justify-content: space-between;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    font-size: 13px;
    font-weight: 400;
    line-height: 15.73px;
    color: #708990;

    & + .header-top__link {
      margin-left: 24px;
      @media (max-width: $mobileMedium){
        margin-left: 12px;
      }
    }
    span{
      display: flex;
    }

    span + span {
      margin-left: 8px;
    }

    &--orange {
      color: #C56940;
    }
  }
}
